<!--
 * @Description: 发布找车、智能填单、新增货物、修改货物
 * @Version: 1.0.0
 * @Autor: gitxinghui
 * @Date: 2020-12-01 01:49:35
 * @LastEditors: gitxinghui
 * @LastEditTime: 2020-12-24 13:02:50
-->
<template>
    <view class="things_publish_freight">
        <!-- tabbar -->
        <cu-custom><block slot="content">货物详情</block></cu-custom>
        <view class="freight_form">
            <form @submit="formSubmit">
                <!-- 地址 -->
                <view class="things_width_full address_box">
                    <view class="flex justify-between align-center cu-form-group start_address">
                        <view class="flex justify-start align-center title title_not"><view class="before_circle"></view>{{wPickerStart.value ? wPickerStart.value : '输入装载地点' }}</view>
                        <input type="text" class="hide" name="loading_region" :value="wPickerStart.value"/>
                    </view>
                    <view class="flex justify-between align-center cu-form-group end_address">
                        <view class="flex justify-start align-center title title_not"><view class="before_circle"></view>{{wPickerEnd.value ? wPickerEnd.value : '输入卸货地点'}}</view>
                        <input type="text" class="hide" name="unloading_region" :value="wPickerEnd.value"/>
                    </view>
                </view>
                <view class="tip_title">填写基本信息</view>
                <view class="things_width_full form_info">
                    <view class="cu-form-group">
                        <view class="title">货物名称：</view>
                        <input class="inputText" placeholder="请填写" placeholder-class="placeHolderText" disabled="true" name="goods_name" :value="freight_form_data.goodsName" />
                    </view>
                    <view class="cu-form-group">
                        <view class="title">运输价格(单价)：</view>
                        <input class="inputText" placeholder="请填写" type="digit" placeholder-class="placeHolderText"  disabled="true" name="unit_price" :value="freight_form_data.unitPrice" />
                    </view>
                    <view class="cu-form-group">
                        <view class="title">货物类型：</view>
                        <input class="inputText" placeholder="请选择" placeholder-class="placeHolderText" :disabled="true" name="goods_type_label" :value="wPickerFreight.label"/>
                        <input class="hide" name="goods_type" :value="wPickerFreight.value"/>
                    </view>
                    <view class="cu-form-group">
                        <view class="title">重量(吨)：</view>
                        <input class="inputText" placeholder="请填写" type="digit" placeholder-class="placeHolderText" disabled="true" name="weight"  :value="freight_form_data.weight" />
                    </view>
                    <view class="cu-form-group">
                        <view class="title">货物介质：</view>
                        <input class="inputText" placeholder="请填写" placeholder-class="placeHolderText" disabled="true" name="goods_medium" :value="freight_form_data.goodsMedium" />
                    </view>
                    <view class="cu-form-group">
                        <view class="title">损耗范围(吨)： </view>
                        <input class="inputText" placeholder="请填写" type="digit" placeholder-class="placeHolderText" disabled="true" name="loss_range" :value="freight_form_data.lossRange"/>
                    </view>
                    <view class="cu-form-group">
                        <view class="title">开始时间：</view>
                        <picker mode="date" name="start_time" :value="start_time" start="2015-09-01" disabled="true" @change="startDateChange">
                            <view class="flex justify-start align-center picker" :class="start_time ? 'inputText':'placeHolderText'">{{start_time ? start_time : '请选择'}}</view>
                        </picker>
                    </view>
                    <view class="cu-form-group">
                        <view class="title">结束时间：</view>
                        <picker mode="date" name="end_time" :value="end_time" start="2015-09-01" disabled="true" @change="endDateChange">
                            <view class="flex justify-start align-center picker" :class="end_time ? 'inputText':'placeHolderText'">{{end_time ? end_time : '请选择'}}</view>
                        </picker>
                    </view>
                    <view class="cu-form-group">
                        <view class="title">折扣比例：</view>
                        <input class="inputText" placeholder="请选择折扣比例" placeholder-class="placeHolderText" :disabled="true" name="radio_type_label" :value="wPickerDiscount.label"/>
                        <input class="hide" name="radio_type" :value="wPickerDiscount.value"/>
                    </view>
                    <view class="cu-form-group remarks_input">
                        <view class="title">备注：</view>
                        <view name="remark" class="inputText text_input">{{freight_form_data.remark}}</view>
                    </view>
                </view>
                <view class="flex justify-center align-center btn_box">
                    <!-- 更新货物 -->
                    <!-- <button class="cu-btn publish_btn" form-type="submit" :disabled="buttonType">更新</button> -->
                </view>
                <!-- 装载-城市三级联动框 -->
                <w-picker 
                    :visible.sync="wPickerStart.visible"
                    mode="region"
                    :value="wPickerStart.value"
                    default-type="name"
                    :level="3"
                    :hide-area="false"
                    @confirm="selectAddressStart($event,'region')"
                    themeColor="#32d08b"
                    ref="regionStart">
                </w-picker>
                <!-- 卸载-城市三级联动框 -->
                <w-picker 
                    :visible.sync="wPickerEnd.visible"
                    mode="region"
                    :value="wPickerEnd.value"
                    default-type="name"
                    :level="3"
                    :hide-area="false"
                    @confirm="selectAddressEnd($event,'region')"
                    themeColor="#32d08b"
                    ref="regionEnd">
                </w-picker>
                <!-- 货物类型 -->
                <w-picker 
                    :visible.sync="wPickerFreight.visible"
                    mode="selector"
                    :value="wPickerFreight.value"
                    default-type="name"
                    :default-props="wPickerFreight.defaultProps"
                    :options="wPickerFreight.list"
                    @confirm="selectFreight($event,'selector')"
                    themeColor="#32d08b"
                    ref="freightPicker">
                </w-picker>
                <!-- 货车类型 -->
                <w-picker 
                    :visible.sync="wPickerCar.visible"
                    mode="selector"
                    :value="wPickerCar.value"
                    default-type="name"
                    :options="wPickerCar.list"
                    @confirm="selectCar($event,'selector')"
                    themeColor="#32d08b"
                    ref="carPicker">
                </w-picker>
                <!-- 折扣类型 -->
                <w-picker 
                    :visible.sync="wPickerDiscount.visible"
                    mode="selector"
                    :options="base_discountData"
                    @confirm="selectDiscount($event,'selector')"
                    themeColor="#32d08b"
                    ref="discountPicker">
                </w-picker>
            </form>
        </view>
        <!-- loading弹框组件 -->
		<popLinkDialog></popLinkDialog>
		<popLoading></popLoading>
		<popDialog></popDialog>
		<tipModal></tipModal>
		<!-- 登录 -->
		<login></login>
    </view>
</template>
<script>
    import { updateGoodsCollection_api,getGoodsCollectionInfo_api } from '@/api/index.js';
	import { appData } from '@/utils/config.js';
	import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
	import storage from '@/utils/storage.js';
    import { identity_format } from '@/utils/filters.js';
    import checkForm from '@/utils/checkFormData.js';
	let that = null;
export default {
    data() {
        return {
            appBaseData:null,//全局配置信息
            userInfo: {},
            goods_id: '',
            wPickerStart: {
                visible: false,
                result: {
                    value: ''
                },
                value: ["广东省","茂名市","茂南区"],
                defaultRegion:["广东省","茂名市","茂南区"]
            },
            wPickerEnd: {
                visible: false,
                value: ["广东省","茂名市","茂南区"],
                defaultRegion:["广东省","茂名市","茂南区"]
            },
            // 货物picker
            wPickerFreight: {
                visible: false,
                value: '',
                label: '',
                list: storage.get("appBaseData").wxapp.goods_type,
                defaultProps: {"label":"name","value":"value"}
            },
            // 货车类型
            wPickerCar: {
                visible: false,
                value: '',
                list: [],
                defaultRegion:''
            },
            // 折扣类型
            wPickerDiscount: {
                visible: false,
                value: '',
                label: '',
                list: [{label:'0.16',value:20},{label:'1.6',value:30},{label:'3.2',value:40},{label:'8',value:50},{label:'16',value:60}],
            },
            // 开始时间
            start_time: '',
            // 结束时间
            end_time: '',
            // 全局按钮状态
            buttonType: false,
            freight_form_data: null
        }
    },
    watch: {
        '$store.getters.base_appBaseData': function(val) {
            that.appBaseData = val;
            that.wPickerFreight.list = val.wxapp.goods_type;
        },
        '$store.getters.base_buttonType': function(val) {
            that.buttonType = val;
        },
    },
    computed: {
        ...mapGetters(['base_discountData']) 
    },
    methods: {
        ...mapMutations([
            'modal',
            'user',
            'base'
        ]),
        /**开始时间选择
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        startDateChange(e) {
            that.start_time = e.detail.value;
        },
        /**结束时间选择
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        endDateChange(e) {
            that.end_time = e.detail.value;
        },
        /**打开货物弹框
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        openWPickerFreight(e) {
            that.wPickerFreight.visible = true;
        },
        /**选择货物
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        selectFreight(e) {
            that.wPickerFreight.label = e.result;
            that.wPickerFreight.value = e.value;
        },
        /**选择车
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        selectCar(e) {
            console.log(e);
        },
        /**选择折扣
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        selectDiscount(e) {
            that.wPickerDiscount.label = e.result;
            that.wPickerDiscount.value = e.value;
        },
        /**装货地点picker打开
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        openWPickerStart(e) {
            that.wPickerStart.visible = true;
        },
        /**装货地点选择
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        selectAddressStart(e) {
            that.wPickerStart.value = e.result;
        },
        /**卸货地点picker打开
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        openWPickerEnd(e) {
            that.wPickerEnd.visible = true;
        },
        /**卸货地点选择
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        selectAddressEnd(e) {
            that.wPickerEnd.value = e.result;
        },
        /**
         * @description: 提交表单
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        formSubmit(e) {
            //定义表单规则
            let rule = [
                {name:"loading_region", checkType : "notnull", checkRule:"",  errorMsg:"请选择装载地址"},
                {name:"unloading_region", checkType : "notnull", checkRule:"",  errorMsg:"请选择卸载地址"},
                {name:"goods_name", checkType : "notnull", checkRule:"",  errorMsg:"请输入货物名称"},
                {name:"unit_price", checkType : "morethanzero",  errorMsg:"请输入货物运输单价"},
                {name:"goods_type_label", checkType : "notnull", checkRule:"",  errorMsg:"请选择货物类型"},
                {name:"weight", checkType : "morethanzero",  errorMsg:"请输入货物重量"},
                {name:"goods_medium", checkType : "notnull", checkRule:"",  errorMsg:"请输入货物介质"},
                {name:"loss_range", checkType : "notnull", checkRule:"",  errorMsg:"请输入损耗范围"},
                {name:"start_time", checkType : "notnull",checkRule:"",  errorMsg:"请选择装载时间"},
                {name:"end_time", checkType : "notnull", checkRule:"",  errorMsg:"请选择卸载时间"},
                {name:"radio_type_label", checkType : "notnull", checkRule:"",  errorMsg:"请选择折扣比例"},
            ];
            //进行表单检查
            let formData = e.detail.value;
            let checkRes = checkForm.check(formData, rule);
            if(!checkRes){
                that.modal({name:'popTipModal',value:{type: true,tipTitle:'表单错误',tipContent:checkForm.error}});
                return false;
            }
            formData.goods_id = that.goods_id;
            that.modal({name:'popLoading',value:true});
            that.base({name:'buttonType',value:true});
            updateGoodsCollection_api(formData,that).then(res=>{
                that.modal({name:'popLoading',value:false});
                that.base({name:'buttonType',value:false});
                that.base({name:'refreshPage',value:true});
                that.modal({name:'popLinkDialog',value:{type:true,tipTitle:'提示',tipContent:'更新成功',linkUrl:1}});
            }).catch(err=>{
                console.log(err);
                that.modal({name:'popLoading',value:false});
                that.base({name:'buttonType',value:false});
            })
        },
        /**获取货物信息
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        getFreightInfo(e) {
            let params = {
                goods_id: that.goods_id
            };
            getGoodsCollectionInfo_api(params,that).then(res=>{
                that.freight_form_data = res;
                that.wPickerStart.value = res.loadingAddress;
                that.wPickerEnd.value = res.unloadingAddress;
                that.wPickerFreight.value = res.goodsTypeId;
                that.wPickerFreight.label = res.goodsType;
                that.start_time = res.startTime;
                that.end_time = res.endTime;
                that.wPickerDiscount.label = res.radioTypeText;
                that.wPickerDiscount.value = res.radioType;
            }).catch(err=>{
                console.log(err)
            })
        }
    },
    // 页面生命周期
    onLoad: function (options) {
        that = this;
        if (!options.goods_id || options.goods_id == ''|| options.goods_id == 'undefined') {
            that.modal({name:'popLinkDialog',value:{type:true,tipTitle:'提示',tipContent:'该货物不存在',linkUrl:1}});
            return false;
        }
        that.goods_id = options.goods_id;
        that.getFreightInfo();
    }
}
</script>

<style lang="scss" scoped>
    @import './index';
</style>
